export default __NODE__ &&
  createPlugin({
    middleware() {
      const parseBody = bodyParser();
      return async (ctx, next) => {
        if (ctx.method === "GET" && ctx.path === "/api/album") {
          await parseBody(ctx, Promise.resolve);

          console.log("getall");
        } else if (ctx.method === "GET" && ctx.path === "/api/album/:id") {
          console.log("getone");
          return next();
        }
      };
    }
  });
